<template>
  <div class="full-height container">
    <AppHeader @reset-todos="clearTodos" />

    <div class="app-container">
      <div class="todo-list">
        <ToDoItem
          class="todo-card"
          v-for="todoItem in todos"
          :key="todoItem.id"
          :todoItem="todoItem"
          @save-todos="saveTodoItem"
          @complete-todo="saveTodoItem"
        />
        <div class="todo-card small-todos-container">
          Links are going to be here soon!
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ToDoItem from '@/components/ToDoItem.vue'
import AppHeader from '@/components/AppHeader.vue'
import STORAGE_KEY from '@/utils/storage-key'
import BASE_TODOS from '@/utils/base-todos'

export default {
  components: { ToDoItem, AppHeader },
  data: function() {
    return {
      todos: this.fetchTodos()
    }
  },
  methods: {
    fetchTodos() {
      return JSON.parse(localStorage.getItem(STORAGE_KEY)) || BASE_TODOS
    },
    clearTodos() {
      this.todos = BASE_TODOS

      this.saveTodos(BASE_TODOS)
    },
    findToDoIndex(todo) {
      return this.todos.findIndex(todoItem => todoItem.id === todo.id)
    },
    saveTodoItem(todo) {
      const todoIndex = this.findToDoIndex(todo)

      this.todos[todoIndex] = todo

      this.saveTodos()
    },
    saveTodos() {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(this.todos))
    }
  }
}
</script>

<style>
.full-height.container {
  min-height: 100vh;
}

.app-container {
  width: 85vw;
  max-width: 900px;
  margin: auto;
}

.todo-list {
  padding: 2rem 1.75em;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.small-todos-container {
  border: solid 1px #979797;
  border-radius: 8px;
  min-height: var(--base-height);
  padding: 1.5rem;
}

@media only screen and (min-width: 750px) {
  .todo-list {
    grid-template-columns: repeat(3, 1fr);
  }

  .todo-list .todo-card:first-of-type {
    grid-column: span 3;
    font-size: 3rem;
  }

  .small-todos-container {
    grid-column: span 3;
  }
}
</style>
